.App {
  font-family: "Vogue", "Times New Roman", Times, serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* font-weight:bold; */
  padding: 10px;
  width: 500px;
  margin: auto;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "notosans";
  src: url("../src/assets/fonts/NotoSansKR-Regular.otf");
  /* font-family: "gothic";
  src: url("../src/assets/fonts/GothicA1-Medium.ttf"); */
}

* {
  font-family: "notosans";
}
